import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { GiftsService } from "app/admin/services/Admin/gifts.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { environment } from "environments/environment";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "app-gifts-add",
  templateUrl: "./gifts-add.component.html",
  styleUrls: ["./gifts-add.component.scss"],
})
export class GiftsAddComponent implements OnInit {
  functionId: number;
  submitted = false;
  giftId: number = 0;
  EduCompId: any;
  picturePath?: any;
  rowFunctions: IRowFunctionVM[];
  myForm!: FormGroup;
  currentLang: string;
  giftName: string;
  giftCategory: any[];
  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private authserv: AuthService,
    private giftsService: GiftsService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private msg: ToastrService
  ) {
    this.route.queryParamMap.subscribe((params) => {
      this.giftId = Number(params.get("giftId"));
      if (this.giftId > 0) {
        this.getGiftById(this.giftId);
      }

      this.functionId = Number(params.get("functionId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //  console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });
  }

  getAllGiftCategory() {
    this.giftsService.getGiftCategoryDropDown(this.EduCompId).subscribe((response: any) => {
      if (response) {
        this.giftCategory = response;
        //     console.log(this.giftCategory);
      }
    });
  }
  get engNameCtrl() {
    return this.myForm.get("englishName");
  }
  get arbNameCtrl() {
    return this.myForm.get("arabicName");
  }
  get categoryCtrl() {
    return this.myForm.get("giftsCategoryId");
  }
  get pointsCtrl() {
    return this.myForm.get("points");
  }
  get qtyCtrl() {
    return this.myForm.get("qty");
  }
  get descriptionCtrl() {
    return this.myForm.get("description");
  }
  get IdCtrl() {
    return this.myForm.get("id");
  }
  ngOnInit() {
    this.EduCompId = this.authserv.getEduCompId();
    this.myForm = this.fb.group({
      id: [""],
      giftPicture: [""],
      englishName: ["", Validators.required],
      arabicName: ["", Validators.required],
      giftsCategoryId: ["", Validators.required],
      points: ["", Validators.required],
      qty: ["", Validators.required],
      description: [""],
      EduCompId: [this.EduCompId],
    });
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });
    this.getAllGiftCategory();
  }

  OnAdd() {
    this.submitted = true;
    let valid = this.myForm.valid;
    if (valid) {
      console.log(this.myForm.value);
      this.spinner.show();
      // console.log("form data", this.myForm.value);
      this.giftsService.addGift(this.myForm.value).subscribe((res: any) => {
        if (res.returnValue == 1) {
          if (this.IdCtrl.value == 0) {
            if (this.currentLang == "ar") {
              this.msg.success("تم إضافة الهدية");
            } else {
              this.msg.success("Gift Added ");
            }
          } else {
            if (this.currentLang == "ar") {
              this.msg.success("تم تعديل الهدية");
            } else {
              this.msg.success("Gift Updated ");
            }
          }
        } else {
          if (this.currentLang == "ar") {
            this.msg.success("هذا الاسم موجود مسبقا ");
          } else {
            this.msg.success("This name is already exist");
          }
        }

        this.spinner.hide();
      });
    }
  }
  get FormCtrls() {
    return this.myForm.controls;
  }
  onChange(event: any) {
    let fileName = <File>event.target.files[0].name;
    let fileSize = <File>event.target.files[0].size;
    let fileType = <File>event.target.files[0].type;
    let LastModified = <File>event.target.files[0].lastModified;
    let LastModifiedDate = <File>event.target.files[0].lastModifiedDate;

    if (event.target.files) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        let fileReder = event.target.result;
        let data = {
          FileAsBase64: fileReder,
          name: fileName,
          size: fileSize,
          type: fileType,
          LastModified: LastModified,
          LastModifiedDate: LastModifiedDate,
        };
        this.FormCtrls.giftPicture.setValue(data);
        this.picturePath = fileReder;
      };
    }
  }

  getGiftById(giftId: number) {
    this.giftsService.getGiftById(giftId).subscribe((res: any) => {
      if (this.currentLang == "ar") {
        this.giftName = res.arabicName;
      } else {
        this.giftName = res.englishName;
      }

      // this.giftId = res.Id;
      this.IdCtrl?.setValue(this.giftId);
      console.log("idc", this.IdCtrl.value);
      this.engNameCtrl.setValue(res?.englishName);
      this.arbNameCtrl.setValue(res?.arabicName);
      this.categoryCtrl.setValue(res?.GiftCategoryId);
      this.pointsCtrl.setValue(res?.points);
      this.qtyCtrl.setValue(res?.qty);
      this.descriptionCtrl.setValue(res?.description);
      this.picturePath = `${environment.apiURL.replace('/api/','')}${res.attach_path}`;
    });
  }

  ngAfterViewInit() {
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
      console.log(this.currentLang);
    });
    this.getGiftById(this.giftId);
  }
  DeletePic() {
    this.spinner.show();
    this.giftsService.deleteAttach(this.IdCtrl.value).subscribe((res: any) => {
      if (res.returnValue == 1) {
        if (this.currentLang == "ar") {
          this.msg.success("تم حذف صورة الهدية");
        } else {
          this.msg.success("Gift Image Deleted ");
        }

        this.picturePath = "";
      } else this.msg.warning(res);
      this.spinner.hide();
    });
  }
}
