import { ActivatedRoute } from "@angular/router";
import { MatDialogRef } from "@angular/material/dialog";
import { MessagesService } from "app/shared/services/messages.service";
import { Component, Inject, OnInit } from "@angular/core";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { NgxSpinnerService } from "ngx-spinner";
import { TranslateService } from "@ngx-translate/core";
import { MAT_DIALOG_DATA } from "@angular/material/dialog";
import {
  IUnitAttachmentAddEditVM,
  IUnitAttachmentGetVM,
} from "app/admin/models/admin/setup/course";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { IUnitDataGeneralVM } from "app/admin/models/admin/setup/course";
import { AppStrings } from "app/shared/data/app-strings";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-add-unit-attachment",
  templateUrl: "./add-unit-attachment.component.html",
  styleUrls: ["./add-unit-attachment.component.scss"],
})
export class AddUnitAttachmentComponent implements OnInit {
  unitId: number = 0;
  subject_id: number;
  attachmentId: number = 0;
  attach: IUnitAttachmentGetVM;
  myForm!: FormGroup;
  submitted = false;
  curLang: string;
  EduCompId: any;
  functionId: number;

  constructor(
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private toastr: MessagesService,
    private courseService: CourseService,
    public authserv: AuthService,
    private spinner: NgxSpinnerService,
    private translate: TranslateService,
    public uploadFileService: UploadFileService,
    public appStrings: AppStrings,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<AddUnitAttachmentComponent>
  ) {
    this.unitId = data.unitId;
    this.subject_id = data.subject_id;
    this.attachmentId = data.attachmentId;

    this.route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    this.EduCompId = this.authserv.getEduCompId();
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [this.attachmentId],
      file_ar_name: ["", [Validators.required]],
      file_en_name: ["", [Validators.required]],
      attachFile: [null, [Validators.required]],
      // attach_path: [null],
      orderNum: ["", [Validators.required, CustomeValidator.bigZero]],
    });

    if (this.attachmentId > 0) this.getAttachmentById();

    // on closing dialog by clicking outside dialog
    this.dialogRef.backdropClick().subscribe((ele) => {
      this.onCloseDialog();
    });
  }

  onCloseDialog() {
    let data: IUnitDataGeneralVM = null;
    if (this.attachmentId) {
      data = {
        id: this.attachmentId,
        ar_name: this.attach.file_ar_name,
        en_name: this.attach.file_en_name,
        type: this.appStrings.stageAttach,
        order: this.attach.orderNum,
      };
    }
    this.dialogRef.close(data);
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  getAttachmentById() {
    this.spinner.show();
    this.courseService
      .getUnitAttachmentById(this.attachmentId)
      .subscribe((res) => {
        if (res) {
          this.myForm.patchValue(res.fileEduUnit[0]);
          this.formCtrls.attachFile.setValue(res.fileEduUnit[0].attach_path);
          this.attach = res.fileEduUnit[0];
          // console.log(this.myForm.value);
        }
        this.spinner.hide();
      });
  }

  onSubmit() {
    this.submitted = true;
    let data: IUnitAttachmentAddEditVM = {
      id: this.formCtrls.id.value,
      ar_name: this.formCtrls.file_ar_name.value,
      en_name: this.formCtrls.file_en_name.value,
      subject_id: this.subject_id,
      unit_id: this.unitId,
      File: this.formCtrls.attachFile.value,
      orderNum: this.formCtrls.orderNum.value,
    };

    // if (
    //   this.formCtrls.attachFile.value == null &&
    // //   this.formCtrls.attach_path.value == null
    // ) {
    //   this.toastr.warning("labels.MustSelectFile");
    //   return;
    // }

    if (this.myForm.valid) {
      this.spinner.show();
      this.courseService.addEditUnitAttachment(data).subscribe((res: any) => {
        if (res.returnValue == 1) {
          data.id == 0
            ? this.toastr.success("General.AddSuccessfully")
            : this.toastr.success("General.UpdeteSuccessfully");
          this.myForm.reset();
          this.uploadFileService.resetFileById("unit-attach");
          this.submitted = false;
          this.myForm.patchValue(res.File_Edu_Unit[0]);
          this.attach = res.File_Edu_Unit[0];
          this.formCtrls.attachFile.setValue(res.File_Edu_Unit[0].attach_path);
          this.attachmentId = this.formCtrls.id.value;
        } else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
    }
  }
}
