import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { giftCategoryViewModel } from "app/admin/models/admin/gifts";
import { GiftsService } from "app/admin/services/Admin/gifts.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "app-gifts-category-index",
  templateUrl: "./gifts-category-index.component.html",
  styleUrls: ["./gifts-category-index.component.scss"],
})
export class GiftsCategoryIndexComponent implements OnInit {
  myForm!: FormGroup;
  p: number = 1;
  itemsCount: any;
  functionId: number;
  currentLang: string;
  EduCompId: any;
  rowFunctions: IRowFunctionVM[];
  giftCategory: giftCategoryViewModel[] = [];
  dataSource: MatTableDataSource<giftCategoryViewModel>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  displayedColumns: string[] = ["#", "englishName", "arabicName", "actions"];
  constructor(
    private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    private giftsService: GiftsService,
    private route: ActivatedRoute,
    private authserv: AuthService,
    private dialog: MatDialog,
    private toastr: ToastrService,
    private translate: TranslateService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      //    console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //  console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });
  }
  get FormCtrls() {
    return this.myForm.controls;
  }
  ngOnInit() {
    this.EduCompId = this.authserv.getEduCompId();
    this.myForm = this.fb.group({
      page: [""],
      englishName: [""],
      arabicName: [""],
      EduCompId: [this.EduCompId],
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });
    this.getGiftsCategoryAll(1);
  }

  ngAfterViewInit() {
    this.EduCompId = this.authserv.getEduCompId();
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
      this.getGiftsCategoryAll(1);
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    /* if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    } */
  }
  getGiftsCategoryButton(p: number) {
    if (!this.FormCtrls.arabicName.value && !this.FormCtrls.englishName.value) {
      if (this.currentLang == "ar") {
        this.toastr.warning("يجب ادخال الاسم بالانجليزي أو الاسم بالعربي");
      } else
        this.toastr.warning("You have to enter Arabic Name or English Name");
      return;
    } else {
      this.getGiftsCategorySearch(p);
    }
  }

  getGiftsCategorySearch(p: number) {
    this.myForm.controls["page"]?.setValue(p);
    this.p = p;
    this.spinner.show();
    this.giftsService
      .getCategoriesSearch(p, this.myForm.value)
      .subscribe((res: any) => {
        this.giftCategory = res.giftcategoryList;
        console.log("categories", this.giftCategory);

        this.dataSource = new MatTableDataSource(res.giftcategoryList);
        this.itemsCount = res.countItems;
        this.spinner.hide();
      });
  }

  getGiftsCategoryAll(p: number) {
    this.myForm.controls["page"]?.setValue(p);
    this.p = p;
    this.spinner.show();
    this.giftsService
      .getCategoriesAll(this.EduCompId, p)
      .subscribe((res: any) => {
        this.giftCategory = res.giftcategoryList;
        //    console.log("categories", this.giftCategory);

        this.dataSource = new MatTableDataSource(res.giftcategoryList);
        this.itemsCount = res.countItems;
        this.spinner.hide();
      });
  }

  openDeleteDialog(giftCategory: giftCategoryViewModel): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: giftCategory.englishName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeGiftCategory(giftCategory);
      });
  }

  removeGiftCategory(giftCategory: giftCategoryViewModel) {
    this.giftsService
      .RemoveGiftCategory(giftCategory.id)
      .subscribe((response: any) => {
        if (response.returnValue == 2) {
          if (this.currentLang == "ar")
            this.toastr.success(
              "لا يمكن حذف هذه الفئة لأنه يوجد هدايا تنتمي اليها"
            );
          else
            this.toastr.success(
              "System Can't delete this category because there are gifts on this category.."
            );
        } else {
          let deletedIndex = this.dataSource.data.findIndex(
            (e) => e.id == giftCategory.id
          );
          this.dataSource.data.splice(deletedIndex, 1);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
          if (this.currentLang == "ar") {
            this.toastr.success("تم حذف الفئة");
          } else this.toastr.success("Gift Category Deleted");
        }
      });
  }

  resetSearch() {
    this.myForm.reset();
    this.getGiftsCategoryAll(1);
  }
}
