import { DatePipe } from "@angular/common";
import { Component, ElementRef, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { ArticlesService } from "app/admin/services/Admin/setup/articles.service";
import { DepartmentService } from "app/admin/services/setup/department.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";

import { EditJobTitleDatesComponent } from "../../../../shared/components/dialogs/edit-job-title-dates/edit-job-title-dates.component";
import {
  IAddEditArticleModel,
  IArticleJobTitleGetVM,
  IArticleModel,
  IArticleViewModel,
} from "../../../models/admin/setup/articles";
import { IDepartmentModel } from "../../../models/setup/department";
import {
  IJobTitleByDeptVM,
  IJobTitleDatesEditVM,
} from "../../../models/setup/job-title";
import { JobTitleService } from "../../../services/setup/job-title.service";

@Component({
  selector: "app-articles",
  templateUrl: "./articles.component.html",
  styleUrls: ["./articles.component.scss"],
})
export class ArticlesSetupComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  pageNum: number = 0;
  curLang: string;
  functionId: number;
  EduCompId: number;
  rowFunctions: IRowFunctionVM[];
  currentDate = new Date();
  displayedColumns: string[] = [
    "#",
    "title",
    "department",
    "jobTitle",
    "option",
  ];
  dataSource: MatTableDataSource<IArticleModel>;
  initTable: boolean = true;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild("saveBtn", { read: ElementRef }) saveBtn: ElementRef;

  deptsList: IDepartmentModel[];
  jobsList: IJobTitleByDeptVM[];

  constructor(
    private fb: FormBuilder,
    private toastr: ToastrService,
    private articleService: ArticlesService,
    public authserv: AuthService,
    private deptServ: DepartmentService,
    private jobServ: JobTitleService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private translate: TranslateService,
    private datePipe: DatePipe
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });
    this.EduCompId = Number(authserv.getEduCompId());

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [0],
      title: ["", [Validators.required, CustomeValidator.whiteSpace]],
      ArticleContent: ["", [Validators.required]],
      depJobIds: [{ value: "", disabled: true }, [Validators.required]],
      departmentId: ["", [Validators.required]],
      from_date: [null],
      to_date: [null],
    });

    this.getAllArticles(1);

    this.getAllDepartments();
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllDepartments();
    });
    this.getAllDepartments();
    this.getAllArticles(1);
  }

  initTableItems() {
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "department":
          return this.curLang == "ar"
            ? item.department?.ar_name
            : item.department?.en_name;
        case "jobTitle":
          return this.curLang == "ar"
            ? item.jobTitle[0]?.ar_name
            : item.jobTitle[0]?.en_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;

    // paginator
    if (this.initTable) {
      this.dataSource.paginator = this.paginator;
      this.initTable = false;
    }
  }

  getAllArticles(pageNumber: number) {
    this.pageNum = pageNumber;
    this.spinner.show();

    this.articleService
      .getAllArticles<IArticleViewModel>(pageNumber, this.EduCompId)
      // .getAllArticles<IArticleViewModel>(this.EduCompId, pageNumber)
      .subscribe((response) => {
        if (response) {
          this.dataSource = new MatTableDataSource(
            response.ArticleList
          );
          this.itemsCounts = response.countItems;

          // console.log("articles: >> ", response);

          this.initTableItems();
        }
        this.spinner.hide();
      });
  }

  getAllDepartments() {
    this.deptServ
      .getDepartmentsDropDown(this.EduCompId)
      .subscribe((response: any) => {
        if (response) {
          this.deptsList = response;
        }
      });
  }

  getTitleByDeptId(id: any) {
    this.FormCtrls.depJobIds.setValue("");
    if (this.FormCtrls.departmentId.value != "") {
      this.FormCtrls.depJobIds.enable();
      this.jobServ
        .getJobTitleByDepartment<IJobTitleByDeptVM[]>(id)
        .subscribe((res: any) => {
          this.jobsList = res;
          this.FormCtrls.depJobIds.enable();
        });
    } else {
      this.FormCtrls.depJobIds.disable();
    }
  }

  onSubmit() {
    this.submitted = true;

    if (this.myForm.valid) {
      this.spinner.show();

      let article = {} as IAddEditArticleModel;
      article.EduCompId = this.EduCompId;
      article.title = this.FormCtrls.title?.value;
      article.ArticleContent = this.FormCtrls.ArticleContent?.value;
      article.depJobIds = this.FormCtrls.depJobIds?.value;

      article.from_date = this.datePipe.transform(
        this.FormCtrls.from_date?.value ?? this.currentDate
      );
      article.to_date = this.datePipe.transform(this.FormCtrls.to_date?.value);

      console.log("edit or add => ", article);

      ////////////////// edit article
      if (this.FormCtrls.id?.value) {
        // use after auth
        // obj.roleId = this.FormCtrls.roleId?.value;

        this.articleService
          .editArticle<IAddEditArticleModel, any>(
            article,
            this.FormCtrls.id?.value
          )
          .subscribe((response) => this.afterAddOrEditArticle(response));
      }
      ////////////////// add article
      else {
        // use after auth
        // obj.roleId = this.FormCtrls.roleId?.value;

        this.articleService
          .addArticle<IAddEditArticleModel, any>(article)
          .subscribe((response) => this.afterAddOrEditArticle(response));
      }
    }
  }

  afterAddOrEditArticle(response: any) {
    this.submitted = false;
    // console.log("article edited or added => ", response);
    if (response && response !== null) {
      if (response.returnValue == 0) {
        this.toastr.error(response.returnString);
      } else {
        this.getAllArticles(this.pageNum);
        // for translate message in toastr
        let mess: string;
        this.translate.get("articles.successMessage").subscribe((data) => {
          mess = data;
        });
        this.toastr.success(mess);

        this.myForm.reset();

        this.translate.get("General.Save").subscribe((data) => {
          this.saveBtn.nativeElement.innerText = data;
        });
      }
    }
    this.spinner.hide();
  }

  editArticle(article: IArticleModel) {
    // console.log(article);
    this.myForm.patchValue(article);
    this.FormCtrls.departmentId?.setValue(article.department.id);

    this.getTitleByDeptId(this.FormCtrls.departmentId?.value);
    this.FormCtrls.depJobIds?.setValue(article.jobTitle.map((j) => j.depJobId));

    this.translate.get("General.Update").subscribe((data) => {
      this.saveBtn.nativeElement.innerText = data;
    });
  }

  openDeleateDialog(article: IArticleModel): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: article.title,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeArticle(article);
      });
  }

  removeArticle(article: IArticleModel) {
    this.articleService.deleteArticle(article.id).subscribe((response: any) => {
      if (response.returnValue == 1) {
        // console.log("articlr to delete", response);
        this.getAllArticles(this.pageNum);

        let mess: string;
        this.translate
          .get("articles.successDeleteMessage")
          .subscribe((data) => {
            mess = data;
          });
        this.toastr.success(mess);
        this.myForm.reset();
        this.submitted = false;
      } else this.toastr.warning(response.returnString);
    });
  }

  editJobTitleDates(
    jobTitle_articleId: number,
    jobtitle: IJobTitleDatesEditVM
  ) {
    this.articleService
      .editJobTitleArticle<IJobTitleDatesEditVM, any>(
        jobTitle_articleId,
        jobtitle
      )
      .subscribe((response) => {
        if (response.returnValue == 1) {
          this.getAllArticles(this.pageNum);

          let mess: string;
          this.translate
            .get("JobTitle.toasterUpdeteSucses")
            .subscribe((data) => {
              mess = data;
            });
          this.toastr.success(mess);
        } else this.toastr.warning(response.returnString);
      });
  }

  openUpdateJobTitleDialog(jobTitle: IArticleJobTitleGetVM) {
    // console.log(jobTitle);
    this.dialog
      .open(EditJobTitleDatesComponent, {
        data: {
          jobTitle: jobTitle,
        },
      })
      .afterClosed()
      .subscribe((response) => {
        if (response) {
          console.log(response.jobTitle);
          this.editJobTitleDates(jobTitle.jobTitle_article, response.jobTitle);
        }
      });
  }

  openDeleteJobtitleDialog(jobTitle: IArticleJobTitleGetVM) {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.curLang == "ar" ? jobTitle.ar_name : jobTitle.en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeJobtitleArticle(jobTitle.jobTitle_article);
      });
  }

  removeJobtitleArticle(jobTitle_article: number) {
    this.articleService
      .deleteJobTitleArticle(jobTitle_article)
      .subscribe((response: any) => {
        if (response.returnValue == 1) {
          this.getAllArticles(this.pageNum);

          let mess: string;
          this.translate.get("articles.deleteJobTitleMsg").subscribe((data) => {
            mess = data;
          });
          this.toastr.success(mess);
          this.myForm.reset();
          this.submitted = false;
        } else this.toastr.warning(response.returnString);
      });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

}
