import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { IDepartmentModel } from "app/admin/models/setup/department";
import {
  IJobTitleByDeptVM,
  IJobTitleDatesEditVM,
} from "app/admin/models/setup/job-title";
import { DepartmentService } from "app/admin/services/setup/department.service";
import { EditJobTitleDatesComponent } from "app/shared/components/dialogs/edit-job-title-dates/edit-job-title-dates.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";

import {
  ICourseGetModel,
  ICourseGetVM,
  ICourseJobTitleGetVM,
  IDiplomaGetVM,
} from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { AuthService } from "app/shared/services/auth/auth.service";
import { ISearchCourse } from "app/admin/models/admin/setup/course";
import { JobTitleService } from "app/admin/services/setup/job-title.service";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { MessagesService } from "app/shared/services/messages.service";

@Component({
  selector: "app-course-index",
  templateUrl: "./course-index.component.html",
  styleUrls: ["./course-index.component.scss"],
})
export class CourseIndexComponent implements OnInit {
  currentLang: string;
  departments: IDepartmentModel[];
  jobTitles: IJobTitleByDeptVM[];
  diplomas: IDiplomaGetVM[];
  EduCompId: number;
  departmentId: number;
  jobId: number;
  totalItems: number;
  initTable: boolean = true;

  displayedColumns: string[] = [
    "#",
    "course_name",
    // "promoLink",
    // "courseDescreption",
    "attach_path",
    "department",
    "jobTitle",
    "actions",
  ];
  dataSource: MatTableDataSource<ICourseGetVM>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  myForm!: FormGroup;

  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any[] = [
  //   { functionName: "Edit" },
  //   { functionName: "Delete" },
  //   { functionName: "UpdateArticalJob" },
  //   { functionName: "DeleteArticalJob" },
  // ];

  constructor(
    private courseServ: CourseService,
    private deptServ: DepartmentService,
    private jobServ: JobTitleService,
    private fb: FormBuilder,
    ar: ActivatedRoute,
    public authserv: AuthService,
    private spinner: NgxSpinnerService,
    private msg: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    public fileService: UploadFileService
  ) {
    ar.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log(`RowFunctions for"${this.functionId}": `, res);
              this.rowFunctions = res;
            }
          });
      }
    });
    this.EduCompId = Number(authserv.getEduCompId());

    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });
  }

  ngOnInit(): void {
    // console.log(this.eduCompId);
    this.myForm = this.fb.group({
      departmentId: [null],
      jobId: [
        {
          value: null,
          disabled: true,
        },
      ],
      diplomaId: [null],
      courseName: [null],
      page: [1],
    });

    // get dapartments && diplomas
    this.getAllDepartments();
    this.getAllDiplomas();

    // initialize dataSource | get all courses
    this.getAllCourses(1);

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllDepartments();
      this.getAllDiplomas();
    });
  }

  customFilter() {
    this.dataSource.filterPredicate = (data: ICourseGetVM, filter) => {
      return (
        data.subCourse_en_name?.toLocaleLowerCase().indexOf(filter) != -1 ||
        data.subCourse_ar_name?.toLocaleLowerCase().indexOf(filter) != -1 ||
        // data.promoLink?.toLocaleLowerCase().indexOf(filter) != -1 ||
        // data.courseDescreption?.toLocaleLowerCase().indexOf(filter) != -1 ||
        data.department
          ?.map((d) => (this.currentLang == "ar" ? d.ar_name : d.en_name))
          .join(" ")
          .toLocaleLowerCase()
          .indexOf(filter) != -1 ||
        data.department
          ?.map((d) =>
            d.jobTitle?.map((j) =>
              this.currentLang == "ar" ? j.ar_name : j.en_name
            )
          )
          .join(" ")
          .toLocaleLowerCase()
          .indexOf(filter) != -1
      );
    };
  }

  initTableItems() {
    // sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "course_name":
          return this.currentLang == "ar"
            ? item.subCourse_ar_name
            : item.subCourse_en_name;
        case "department":
          return this.currentLang == "ar"
            ? item.department[0].ar_name
            : item.department[0].en_name;
        case "jobTitle":
          return this.currentLang == "ar"
            ? item.department[0].jobTitle[0]?.ar_name
            : item.department[0].jobTitle[0]?.en_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;

    // paginator
    if (this.initTable) {
      this.dataSource.paginator = this.paginator;
      this.initTable = false;
    }
  }

  getAllCourses(p: number) {
    this.page?.setValue(p);

    let searchData: ISearchCourse = {
      depId: this.FormCtrls.departmentId.value || 0,
      jobId: this.FormCtrls.jobId.value || 0,
      // course
      subCourseName:
        this.FormCtrls.courseName.value?.trim().toLowerCase() || null,
      // diploma
      diploma_id: this.FormCtrls.diplomaId.value || 0,
      page: this.page.value,
      eduCompId: this.EduCompId || 0,
    };
    // console.log(searchData);

    this.spinner.show();
    this.courseServ
      .getAllCoursesBySearch<ICourseGetModel>(searchData)
      .subscribe((response) => {
        if (response) {
          let courses = response.subCourseList;
          // put jobTitles of the same department in one array
          courses = courses?.map((course) => {
            let depts = [];
            course.department?.forEach((d) => {
              let index = depts.findIndex((i) => i.id == d.id);
              if (index == -1) depts.push(d);
              else depts[index].jobTitle.push(d.jobTitle[0]);
            });
            course.department = depts;
            return course;
          });
          // console.log(courses);

          this.dataSource = new MatTableDataSource(courses);
          this.totalItems = response.countItems;

          this.customFilter();
          this.initTableItems();
        }
        this.spinner.hide();
      });
  }

  getAllDepartments() {
    this.deptServ
      .getDepartmentsDropDown<IDepartmentModel[]>(this.EduCompId)
      .subscribe((response: any) => {
        if (response) {
          this.departments = response;
        }
      });
  }

  getAllDiplomas() {
    this.courseServ
      .getAllDiplomasDropDown<IDiplomaGetVM[]>(this.EduCompId)
      .subscribe((response) => {
        if (response) {
          this.diplomas = response;
        }
      });
  }

  onSelectDepartment() {
    this.FormCtrls?.jobId.setValue("");
    if (this.FormCtrls?.departmentId.value) {
      this.jobServ
        .getJobTitleByDepartment<IJobTitleByDeptVM[]>(
          this.FormCtrls?.departmentId.value
        )
        .subscribe((response) => {
          if (response) {
            this.jobTitles = response;

            this.FormCtrls.jobId?.enable();
          }
        });
    } else {
      this.FormCtrls?.jobId.disable();
    }
  }

  openDeleteDialog(course: ICourseGetVM): void {
    let courseName =
      this.currentLang == "ar"
        ? course.subCourse_ar_name
        : course.subCourse_en_name;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: courseName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeCourse(course.id);
      });
  }

  removeCourse(id: number) {
    this.courseServ.removeCourse(id).subscribe((response) => {
      if (response) {
        this.getAllCourses(this.page.value);
        this.msg.success("General.DeleteSuccessfully");
      }
    });
  }

  openUpdateJobTitleDialog(jobTitle: ICourseJobTitleGetVM) {
    // console.log(jobTitle);
    this.dialog
      .open(EditJobTitleDatesComponent, {
        data: {
          jobTitle: jobTitle,
        },
      })
      .afterClosed()
      .subscribe((response) => {
        if (response) {
          // console.log(response.jobTitle);
          this.editJobTitleDates(jobTitle.jobTitleCourseId, response.jobTitle);
        }
      });
  }

  editJobTitleDates(
    jobTitle_articleId: number,
    jobtitle: IJobTitleDatesEditVM
  ) {
    this.courseServ
      .updateJobTitleCourse<IJobTitleDatesEditVM, any>(
        jobTitle_articleId,
        jobtitle
      )
      .subscribe((response) => {
        if (response.returnValue == 1) {
          this.getAllCourses(this.page.value);
          this.msg.success("General.UpdeteSuccessfully");
        } else this.msg.warning(response?.returnString);
      });
  }

  openDeleteJobtitleDialog(jobTitle: ICourseJobTitleGetVM) {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.currentLang == "ar" ? jobTitle.ar_name : jobTitle.en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.removeJobtitleArticle(jobTitle.jobTitleCourseId);
      });
  }

  removeJobtitleArticle(jobTitleCourseId: number) {
    this.courseServ
      .removeJobTitleCourse(jobTitleCourseId)
      .subscribe((response: any) => {
        if (response.returnValue == 1) {
          this.getAllCourses(this.page.value);
          this.msg.success("General.DeleteSuccessfully");
        } else this.msg.warning(response?.returnString);
      });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }
  get page() {
    return this.myForm.get("page");
  }

  resetSearch() {
    this.myForm.reset();
    this.FormCtrls?.jobId.disable();
    this.getAllCourses(1);
  }
}
