import { StructureIndexComponent } from "./courses/course-structure/structure-index/structure-index.component";
import { JobTitleComponent } from "./job-titles/job-title/job-title.component";
import { CategorieComponent } from "./categrioes/categorie.component";
import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { IsAdminGuard } from "app/shared/services/auth/Guards/roles/is-admin.guard";
import { ManualCashPaymentComponent } from "./manual-cash-payment/manual-cash-payment.component";
import { GenerateBalanceCodesComponent } from "./generate-balance-codes/generate-balance-codes.component";
import { ManageViewsComponent } from "./manage-views/manage-views.component";
import { SendWhatsappMsgComponent } from "./send-whatsapp-msg/send-whatsapp-msg.component";
import { ExceptionalViewsComponent } from "./exceptional-views/exceptional-views.component";
import { SendSingularWhatsappMsgComponent } from "./send-singular-whatsapp-msg/send-singular-whatsapp-msg.component";
import { AnnouncementSetupComponent } from "./announcement-setup/announcement-setup.component";
import { PointsSetupComponent } from "./points-setup/points-setup.component";
import { DepartmentComponent } from "./Department/department.component";
import { AddCourseComponent } from "./courses/add-course/add-course.component";
import { CourseIndexComponent } from "./courses/course-index/course-index.component";
import { ArticlesSetupComponent } from "./articles-setup/articles.component";
import { AnnouncementIndexComponent } from "./announcement-index/announcement-index.component";
import { GiftsCategoryIndexComponent } from "./gifts-category/gifts-category-index/gifts-category-index.component";
import { GiftsCategoryAddComponent } from "./gifts-category/gifts-category-add/gifts-category-add.component";
import { GiftsIndexComponent } from "./gifts/gifts-index/gifts-index.component";
import { GiftsAddComponent } from "./gifts/gifts-add/gifts-add.component";
import { DiplomaComponent } from "./courses/diploma/diploma.component";
import { AddUsersToCourseComponent } from "./courses/add-users-to-course/add-users-to-course.component";
import { CourseRequestComponent } from "./courses/course-requestes/course-requestes.component";
import { CourseObjectivesComponent } from "./courses/course-objectives/course-objectives.component";

const routes: Routes = [
  {
    path: "manual-cash-payment",
    component: ManualCashPaymentComponent,
  },

  {
    path: "generate-balance-codes",
    component: GenerateBalanceCodesComponent,
  },

  {
    path: "manage-views",
    component: ManageViewsComponent,
  },

  {
    path: "send-whatsapp-msg",
    component: SendWhatsappMsgComponent,
  },

  {
    path: "send-singular-whatsapp-msg",
    component: SendSingularWhatsappMsgComponent,
  },

  {
    path: "exceptional-views",
    component: ExceptionalViewsComponent,
  },

  {
    path: "announcement-index",
    component: AnnouncementIndexComponent,
  },

  {
    path: "announcement-setup",
    component: AnnouncementSetupComponent,
  },

  {
    path: "points/:courseId",
    component: PointsSetupComponent,
  },

  { path: "categories", component: CategorieComponent },

  {
    path: "department",
    component: DepartmentComponent,
  },

  {
    path: "articles",
    component: ArticlesSetupComponent,
  },

  {
    path: "course",
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: "full",
      },
      {
        path: "index",
        component: CourseIndexComponent,
      },
      {
        path: "diploma",
        component: DiplomaComponent,
      },
      {
        path: "edit-course/:courseId",
        component: AddCourseComponent,
      },
      {
        path: "add-course",
        component: AddCourseComponent,
      },
      {
        path: "add-employee-to-course/:courseId",
        component: AddUsersToCourseComponent,
      },
      {
        path: "course-request",
        component: CourseRequestComponent,
      },
      {
        path: "course-structure/:courseId",
        component: StructureIndexComponent,
      },
      {
        path: "course-objectives/:courseId",
        component: CourseObjectivesComponent,
      },
    ],
  },

  {
    path: "job-title",
    component: JobTitleComponent,
  },
  {
    path: "gift-category",
    component: GiftsCategoryIndexComponent,
  },
  {
    path: "add-gift-category",
    component: GiftsCategoryAddComponent,
  },
  {
    path: "gift-index",
    component: GiftsIndexComponent,
  },
  {
    path: "gift-add",
    component: GiftsAddComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SetupRoutingModule {}
