import { ActivatedRoute } from "@angular/router";
import { CustomeValidator } from "../../../../shared/validators/customeValid.validator";
import { MatDialog } from "@angular/material/dialog";
import { Component, OnInit, ViewChild, AfterViewInit } from "@angular/core";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  RequiredValidator,
  Validators,
} from "@angular/forms";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import {
  Category,
  CategoriesViewModel,
  IAddCategoryVM,
} from "app/admin/models/setup/categories";
import { CategoriesService } from "app/admin/services/setup/categories.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";
import { TranslateService } from "@ngx-translate/core";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
@Component({
  selector: "app-categorie",
  templateUrl: "./categorie.component.html",
  styleUrls: ["./categorie.component.scss"],
})
export class CategorieComponent implements OnInit {
  inputForm!: FormGroup;
  submitted: boolean = false;
  displayedColumns: string[] = ["id",  "EnglishName","ArabicName", "options"];
  dataSource: MatTableDataSource<Category>;
  itemsCounts: number = 0;
  all: Category[] = [];
  functionId: number;
  EduCompId: number;
  rowFunctions: IRowFunctionVM[];

  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  curLang: string;
  constructor(
    private fb: FormBuilder,
    private _serviceCategories: CategoriesService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private route: ActivatedRoute,
    public authService: AuthService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });
    this.EduCompId = Number(authService.getEduCompId());

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.getCategories(1);
    this.inputForm = this.fb.group({
      id: [0],
      ar_name: [null, [Validators.required, CustomeValidator.whiteSpace]],
      en_name: [null, [Validators.required, CustomeValidator.whiteSpace]],
    });
  }

  get FormCtrls() {
    return this.inputForm.controls;
  }

  ngAfterViewInit() {
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getCategories(1);
    });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getCategories(page: number) {
    this.spinner.show();
    return this._serviceCategories
      .getCategories<CategoriesViewModel>(page, this.EduCompId)
      .subscribe((data) => {
        if (data) {
          this.dataSource = new MatTableDataSource(
            data.courseCategoryList
          );
          this.itemsCounts = data.countItems;
        }
        this.spinner.hide();
      });
  }

  addCategories() {
    this.submitted = true;
    const model: IAddCategoryVM = {
      id: this.inputForm.value.id,
      EduCompId: this.EduCompId,
      ar_name: this.inputForm.value.ar_name,
      en_name: this.inputForm.value.en_name,
    };
    if (this.inputForm.valid) {
      this.spinner.show();
      this._serviceCategories.addCategories(model).subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.submitted = false;
          this.getCategories(1);
          this.toastr.success("تم حفظ الداتا بنجاح");
          this.inputForm.reset();
          this.FormCtrls.id.setValue(0);
        } else this.toastr.error(res.returnString);
      });
      this.spinner.hide();
    }
  }

  updateCategories() {
    this.submitted = true;
    const model: IAddCategoryVM = {
      id: this.inputForm.value.id,
      EduCompId: this.EduCompId,
      ar_name: this.inputForm.value.ar_name,
      en_name: this.inputForm.value.en_name,
    };
    this.spinner.show();
    this._serviceCategories
      .editCategories(model, model.id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.submitted = false;
          this.getCategories(1);
          this.inputForm.reset();
          this.FormCtrls.id.setValue(0);
          this.toastr.success("تم تعديل الداتا بنجاح");
        } else this.toastr.error(res.returnString);
      });
    this.spinner.hide();
  }

  edit(category: Category) {
    this.inputForm.patchValue(category);
  }

  deleteCategories(category: Category) {
    this._serviceCategories
      .deleteCategories(category.id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let i = this.dataSource.data.findIndex((e) => e == category);
          this.dataSource.data.splice(i, 1);
          this.dataSource.paginator = this.paginator;
          this.translate
            .get("categories.toasterdeleteSucses")
            .subscribe((data) => {
              this.toastr.success(data);
            });
        } else this.toastr.error(res.returnString);
      });
  }

  openDeleateDialog(category: Category): void {
    console.log(category);
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.curLang == "ar" ? category.ar_name : category.en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteCategories(category);
      });
  }
}
