import { Component, OnInit } from "@angular/core";
import { FormBuilder, Validators, FormGroup } from "@angular/forms";
import { WhatsAppService } from "./../../../services/whatsApp/whats-app.service";
import { AuthService } from "./../../../../shared/services/auth/auth.service";
import { ManageViewsService } from "app/admin/services/Admin/manage-views.service";
import { NgxSpinnerModule } from "ngx-spinner";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";
import { TranslateService } from "@ngx-translate/core";
import { formValidationService } from "./../../../../shared/services/formValidation.service";

@Component({
  selector: "app-send-singular-whatsapp-msg",
  templateUrl: "./send-singular-whatsapp-msg.component.html",
  styleUrls: ["./send-singular-whatsapp-msg.component.scss"],
})
export class SendSingularWhatsappMsgComponent implements OnInit {
  EduCompId: any;
  myForm: FormGroup;
  searchForm: FormGroup;
  searchResponse: any;
  errors: string[] = [];

  constructor(
    private fb: FormBuilder,
    private whatsappService: WhatsAppService,
    private authService: AuthService,
    private SearchStudentService: ManageViewsService,
    private spinner: NgxSpinnerService,
    private formValidationService: formValidationService,
    private toastr: ToastrService,
    private translate: TranslateService
  ) {}

  ngOnInit(): void {
    this.EduCompId = this.authService.getEduCompId();

    this.myForm = this.fb.group({
      userId: ["", Validators.required],
      AdminEducationCompanyId: [this.EduCompId, Validators.required],
      msg: ["", Validators.required],
      sendToStudentWhatsApp: [false],
      sendToFatherWhatsApp: [false],
      sendToMotherWhatsApp: [false],
    });

    this.searchForm = this.fb.group({
      sentCenterCode: [""],
      sentMobile: [""],
      sentEmail: [""],
    });
  }

  onSearch() {
    //form empty?
    if (!this.formValidationService.isFormEmpty(this.searchForm)) {
      this.SearchStudentService.searchStudent(
        this.EduCompId,
        this.searchForm.value
      ).subscribe((response: any) => {
        if (response.returnValue == 1) {
          //show response and set the userId with the coresponding value
          this.searchResponse = response;
          this.myForm.controls.userId.setValue(response.userId);
        }
      });
    } else this.toastr.error(this.translate.instant("Response.EmptyForm"));
  }

  onSubmit(event: any) {
    //send message request
    event.target.disabled = true;

    this.whatsappService
      .sendSingularWhatsAppMsg(this.myForm.value)
      .subscribe((response) => {
        event.target.disabled = false;
        if (response) {
          console.log(response);
        }
      });
  }
}
