import {
  IDepartmentModel,
  IDepartmentViewModel,
} from "app/admin/models/setup/department";
import {
  Component,
  OnInit,
  ViewChild,
  AfterViewInit,
  ElementRef,
} from "@angular/core";
import { DepartmentService } from "app/admin/services/setup/department.service";
import {
  FormGroup,
  FormBuilder,
  Validators,
  AbstractControl,
} from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { TranslateService } from "@ngx-translate/core";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { AuthService } from "app/shared/services/auth/auth.service";
import { ActivatedRoute } from "@angular/router";
import { IRowFunctionVM } from "app/shared/models/general/general";

@Component({
  selector: "app-department",
  templateUrl: "./department.component.html",
  styleUrls: ["./department.component.scss"],
})
export class DepartmentComponent implements OnInit {
  departments: IDepartmentModel[];
  myForm: FormGroup;
  displayedColumns: string[] = ["#", "enName", "arName", "action"];
  dataSource: MatTableDataSource<IDepartmentModel>;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  itemsCounts: number = 0;
  submitted: boolean = false;
  @ViewChild("addBtn", { read: ElementRef }) addBtn: ElementRef;
  curLang: string;
  functionId: number;
  EduCompId: number;
  rowFunctions: IRowFunctionVM[];
  constructor(
    private departmentService: DepartmentService,
    private fb: FormBuilder,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private route: ActivatedRoute,
    public authService: AuthService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });

    this.EduCompId = Number(authService.getEduCompId());
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.getAllDepartments(1);

    this.myForm = this.fb.group({
      id: [0],
      ar_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      en_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  ngAfterViewInit() {
    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllDepartments(1);
    });

    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getAllDepartments(page: number) {
    this.spinner.show();
    this.departmentService
      .getAllDepartments<IDepartmentViewModel>(page, this.EduCompId)
      .subscribe((res) => {
        if (res) {
          this.dataSource = new MatTableDataSource(
            res.depratmentsList
          );
          this.itemsCounts = res.countItems;
        }
        this.spinner.hide();
      });
  }

  onSubmit() {
    this.submitted = true;
    let data = {} as IDepartmentModel;
    data.id = this.FormCtrls.id.value;
    data.ar_name = this.FormCtrls.ar_name.value;
    data.en_name = this.FormCtrls.en_name.value;
    data.EduCompId = this.EduCompId;

    if (this.myForm.valid) {
      if (data.id == 0) this.addDepartment(data);
      else this.updateDepartment(data, data.id);
    }
  }

  addDepartment(data: IDepartmentModel) {
    this.spinner.show();
    this.departmentService.addDepartment(data).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.myForm.reset();
        this.FormCtrls.id.setValue(0);
        this.getAllDepartments(1);
        this.translate.get("Departments.toasterAddSucses").subscribe((data) => {
          this.toastr.success(data);
        });
        this.submitted = false;
        this.spinner.hide();
      } else this.toastr.warning(res.returnString);
    });
  }

  updateDepartment(data: IDepartmentModel, id: number) {
    this.spinner.show();
    this.departmentService.editDepartment(data, id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.myForm.reset();
        this.FormCtrls.id.setValue(0);
        this.getAllDepartments(1);
        this.translate
          .get("Departments.toasterUpdeteSucses")
          .subscribe((data) => {
            this.toastr.success(data);
          });
        this.submitted = false;
        this.spinner.hide();
      } else this.toastr.warning(res.returnString);
    });
  }

  //start edit
  getDepartmentById(department: IDepartmentModel) {
    this.myForm.patchValue(department);
  }

  // start delete
  deleteDepartment(department: IDepartmentModel) {
    this.departmentService
      .deleteDepartment(department.id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let i = this.dataSource.data.findIndex((e) => e == department);
          this.dataSource.data.splice(i, 1);
          this.dataSource.paginator = this.paginator;
          this.translate
            .get("Departments.toasterdeleteSucses")
            .subscribe((data) => {
              this.toastr.success(data);
            });
          this.getAllDepartments(1);
        } else {
          this.toastr.error(res.returnString);
        }
      });
  }

  openDeleateDialog(department: IDepartmentModel): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.curLang == "ar" ? department.ar_name : department.en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteDepartment(department);
      });
  }
  // end delete
}
