import { environment } from "environments/environment";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class CategoriesService {
  constructor(private _http: HttpClient) {}

  getCategories<T>(page: number, EduCompId: number) {
    return this._http.get<T>(
      `${environment.apiURL}courseCategory/getAllCategories/${EduCompId}/${page}`
    );
  }

  deleteCategories(id: number) {
    return this._http.delete(
      ` ${environment.apiURL}courseCategory/DeleteCategories/${id}`
    );
  }

  editCategories<T>(model: T, id: number) {
    return this._http.put<T>(
      `${environment.apiURL}courseCategory/PutCategories/${id}`,
      model
    );
  }

  addCategories<T>(model: T) {
    return this._http.post<T>(
      `${environment.apiURL}courseCategory/PostCategories/`,
      model
    );
  }
}
