import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { AnnouncementVM } from "app/admin/models/admin/AnnouncementVM";
import { AnnouncementService } from "app/admin/services/Admin/announcement.service";
import {
  IRowFunctionVM,
  teacherByEduCompId,
} from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";
import { GeneralService } from "app/shared/services/General/general.service";
@Component({
  selector: "app-announcement-index",
  templateUrl: "./announcement-index.component.html",
  styleUrls: ["./announcement-index.component.css"],
})
export class AnnouncementIndexComponent implements OnInit {
  myForm!: FormGroup;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  p: number = 1;
  maxSize = 9;
  itemsCount: any;
  EduCompId: any;
  Teachers: teacherByEduCompId[] = [];
  displayedColumns: string[] = [
    "announcementText",
    "fromDate",
    "toDate",
    "img",
    "actions",
  ];
  dataSource: MatTableDataSource<AnnouncementVM>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private toastr: ToastrService,
    private authserv: AuthService,
    private dialog: MatDialog,
    private GeneralService: GeneralService,
    private announcementService: AnnouncementService,
    private spinner: NgxSpinnerService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //  console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  ngOnInit() {
    console.log("helo");
    this.EduCompId = this.authserv.getEduCompId();
    this.myForm = this.fb.group({
      teacherUserId: [""],
      EduCompId: [this.EduCompId],
      page: [""],
    });
    this.getTeachersByEducompId();
    // this.getTeacherAnnouncement(1);
  }
  get EduCompIdCtrl() {
    return this.myForm.get("EduCompId");
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.EduCompIdCtrl.setValue(e.EduCompId);
      this.getTeacherAnnouncement(1);
      this.getTeachersByEducompId();
    });

    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  getTeacherAnnouncement(p: number) {
    console.log(this.myForm.controls["teacherUserId"].value);
    this.myForm.controls["page"]?.setValue(p);
    this.p = p;
    this.spinner.show();
    //console.log("form value",this.myForm.value);
    if (this.myForm.controls["teacherUserId"].value != "") {
      this.announcementService
        .getAllAnnouncements(this.myForm.controls["teacherUserId"].value)
        .subscribe((res: any) => {
          // console.log("groups", res);
          this.dataSource = new MatTableDataSource(res);
          this.itemsCount = res.length;
          this.spinner.hide();
        });
    }
  }
  getTeachersByEducompId() {
    this.GeneralService.getTeachersByEducompId(this.EduCompId).subscribe(
      (res: any) => {
        this.Teachers = res.lstTeachers;
      }
    );
  }
  openDeleateDialog(group: AnnouncementVM): void {
    // this.dialog
    //   .open(DeleteDialogComponent, {
    //     data: {
    //       msg: group.Name,
    //     },
    //   })
    //   .afterClosed()
    //   .subscribe((confirm) => {
    //     if (confirm) this.removeGroup(group);
    //   });
  }
}
