import { DatePipe } from "@angular/common";
import { Component, Inject, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import { TranslateService } from "@ngx-translate/core";
import {
  IJobTitleDepartmentVM,
  IUpdateJobTitleDepartmentVM,
} from "app/admin/models/setup/job-title";
import { JobTitleService } from "app/admin/services/setup/job-title.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "app-update-job-department",
  templateUrl: "./update-job-department.component.html",
  styleUrls: ["./update-job-department.component.scss"],
})
export class UpdateJobDepartmentComponent implements OnInit {
  curLang: string;
  myForm: FormGroup;
  department: IJobTitleDepartmentVM;
  constructor(
    private translate: TranslateService,
    private fb: FormBuilder,
    private jobTitleService: JobTitleService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private datePipe: DatePipe,
    public dialogRef: MatDialogRef<UpdateJobDepartmentComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    this.curLang = translate.currentLang;
    this.department = data.department;
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      from_date: [null, [Validators.required]],
      to_date: [null],
    });
    this.myForm.patchValue(this.department);
  }

  get formCtrl() {
    return this.myForm.controls;
  }

  update_JobTitle_Department() {
    let department: IUpdateJobTitleDepartmentVM = {
      from_date: this.datePipe.transform(this.formCtrl.from_date.value),
      to_date: this.datePipe.transform(this.formCtrl.to_date.value),
    };
    if (this.formCtrl.from_date.value == null) {
      this.toastr.warning("Must Select From Date");
      return;
    }

    if (this.myForm.valid) {
      this.spinner.show();
      this.jobTitleService
        .update_JobTitle_Department(
          department,
          this.department.job_title_dep_id
        )
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            this.dialogRef.close();
            this.toastr.success("تم حفظ البيانات بنجاح");
          } else this.toastr.warning(res.returnString);
        });
      this.spinner.hide();
    }
  }
}
