import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  IAddEditUserVM,
  IGetUserCheckVM,
  IGetUsersVM,
  IuserAddFile,
  IUserListGetVM,
  searchUserVM,
} from "app/admin/models/admin/setup/course";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";

import { CourseService } from "./../../../../services/Admin/setup/course.service";

@Component({
  selector: "app-add-users-to-course",
  templateUrl: "./add-users-to-course.component.html",
  styleUrls: ["./add-users-to-course.component.scss"],
})
export class AddUsersToCourseComponent implements OnInit {
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  subCourseId: number = 0;
  EduCompId: number;
  pageNum: number;
  itemCount: number = 0;
  courseName: string;
  ExcelFile: any = null;
  curLang: string;

  dataSource: MatTableDataSource<IUserListGetVM>;
  displayedColumns: string[] = [
    "#",
    "name",
    "account_email",
    "mobile",
    "actions",
  ];
  initTable: boolean = false;
  myForm!: FormGroup;
  // lastUserIdSearch: number;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild("file") file;

  constructor(
    private authserv: AuthService,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private courseService: CourseService,
    private toastr: ToastrService,
    private dialog: MatDialog,
    private spinner: NgxSpinnerService,
    private translate: TranslateService
  ) {
    route.paramMap.subscribe((params) => {
      this.subCourseId = Number(params.get("courseId"));
    });

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      this.courseName = params.get("courseName");
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              // console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit() {
    this.EduCompId = Number(this.authserv.getEduCompId());

    this.myForm = this.fb.group({
      // page: [1],
      // subCourseId: [this.subCourseId],
      // EduCompId: [this.EduCompId],
      account_email: [null],
      mobile: [null],
      code: [null],
    });

    this.getAllUsers(1);
  }
  // get EduCompIdCtrl() {
  //   return this.myForm.get("EduCompId");
  // }
  // get courseIdCtrl() {
  //   return this.myForm.get("subCourseId");
  // }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      // this.EduCompIdCtrl.setValue(e.EduCompId);
      this.getAllUsers(1);
    });

    // this.authserv.branchId.subscribe((e) => {
    //   this.getAllUsers(1);
    // });
  }

  initTableItems() {
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "name":
          return this.curLang == "ar" ? item.ar_name : item.en_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;

    if (!this.initTable) {
      this.initTable = true;
      this.dataSource.paginator = this.paginator;
    }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  getAllUsers(page: number) {
    this.pageNum = page;

    this.spinner.show();
    this.courseService
      .getAllUsers<IGetUsersVM>(page, this.subCourseId)
      .subscribe((res) => {
        // console.log("Users", res.subUserList);
        this.dataSource = new MatTableDataSource(res.subUserList);
        this.itemCount = res.countItems;

        this.initTableItems();

        this.spinner.hide();
      });
  }

  checkIfUserExist() {
    if (
      !this.formCtls.account_email.value &&
      !this.formCtls.mobile.value &&
      !this.formCtls.code.value
    ) {
      this.translate.get("course.enterOneField").subscribe((data) => {
        this.toastr.warning(data);
      });
      return;
    }

    let obj: searchUserVM = {
      account_email: this.formCtls.account_email.value?.trim() || null,
      mobile: this.formCtls.mobile.value?.trim() || null,
      code: this.formCtls.code.value || null,
    };
    this.courseService
      .checkUser<searchUserVM, IGetUserCheckVM>(obj, this.EduCompId)
      .subscribe((user: IGetUserCheckVM) => {
        // console.log("check user", user);
        if (user) {
          // this.lastUserIdSearch = user.userId;
          this.translate.get("course.existUser").subscribe((data) => {
            this.toastr.info(data);
          });
          this.spinner.hide();
        } else {
          this.translate.get("course.notExistUser").subscribe((data) => {
            this.toastr.info(data);
          });
          this.spinner.hide();
        }
      });
  }

  addUser() {
    if (
      !this.formCtls.account_email.value &&
      !this.formCtls.mobile.value &&
      !this.formCtls.code.value
    ) {
      this.translate.get("course.enterOneField").subscribe((data) => {
        this.toastr.warning(data);
      });
      return;
    }

    let obj: searchUserVM = {
      account_email: this.formCtls.account_email.value?.trim() || null,
      mobile: this.formCtls.mobile.value?.trim() || null,
      code: this.formCtls.code.value || null,
    };
    this.spinner.show();
    // console.log("form values add", this.myForm.value);
    this.courseService
      .checkUser<searchUserVM, IGetUserCheckVM>(obj, this.EduCompId)
      .subscribe((user: IGetUserCheckVM) => {
        // console.log("check user", user);
        if (!user) {
          this.translate.get("course.notExistUser").subscribe((data) => {
            this.toastr.info(data);
          });
          this.spinner.hide();
          return;
        } else {
          let obj: IAddEditUserVM = {
            userId: user.userId,
            subject_id: this.subCourseId,
          };
          this.spinner.show();
          this.courseService
            .addUser<IAddEditUserVM, any>(obj)
            .subscribe((res: any) => {
              // console.log('adduser', res);
              if (res.returnValue == 0) this.toastr.error(res.returnString);
              else {
                this.translate
                  .get("course.addEditUserMsg")
                  .subscribe((data) => {
                    this.toastr.success(data);
                  });
                this.myForm.reset();
                this.getAllUsers(this.pageNum);
              }
              this.spinner.hide();
            });
        }
      });
  }

  openDeleateDialog(user: IUserListGetVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.curLang == "ar" ? user.ar_name : user.en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteUser(user);
      });
  }

  deleteUser(user: IUserListGetVM) {
    this.spinner.show();
    this.courseService.deleteUser(user.id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        // let i = this.dataSource.data.findIndex((e) => e.id == user.id);
        // this.dataSource.data.splice(i, 1);
        this.getAllUsers(this.pageNum);
        this.dataSource.paginator = this.paginator;
        this.translate.get("course.deleteUserMsg").subscribe((data) => {
          this.toastr.success(data);
        });
      } else this.toastr.error(res.returnString);
      this.spinner.hide();
    });
  }

  uploadUsersByExcel() {
    // console.log(this.ExcelFile);
    if (this.ExcelFile == null) {
      this.translate
        .get("course.excelSheet")
        .subscribe((data) => this.toastr.info(data));
      return;
    } else {
      this.spinner.show();
      this.courseService
        .uploadUsersByExcel<IuserAddFile>({
          uploadedFile: this.ExcelFile,
          subject_id: this.subCourseId,
        })
        .subscribe((res: any) => {
          // console.log(res);
          if (
            res?.returnValue == 1 ||
            (res?.isSendAllSuccess == false && res?.errors.length == 0)
          ) {
            this.translate
              .get("course.addExcelSheetUser")
              .subscribe((data) => this.toastr.success(data));
            this.getAllUsers(this.pageNum);
          } else if (res?.returnValue == 0)
            this.toastr.error(res?.returnString);
          else if (res?.isSendAllSuccess == false) {
            this.toastr.info(res?.errors);
            this.getAllUsers(this.pageNum);
          } else if (res?.isUploaded == false) this.toastr.error(res?.errors);
          this.spinner.hide();

          this.file.nativeElement.value = "";
          this.ExcelFile = null;
        });
    }
  }

  onChangeFile(event: any) {
    let fileName = event.target.files[0].name;
    let fileSize = <File>event.target.files[0].size;
    let fileType = <File>event.target.files[0].type;
    let LastModified = <File>event.target.files[0].lastModified;
    let LastModifiedDate = <File>event.target.files[0].lastModifiedDate;

    if (event.target.files) {
      var reader = new FileReader();

      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        let fileReder = event.target.result;

        let data = {
          FileAsBase64: fileReder.replace(
            "data:application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;base64,",
            ""
          ),
          name: fileName.substring(0, fileName.indexOf(".")),
          size: fileSize,
          type: fileName.substring(fileName.indexOf(".")),
          LastModified: LastModified,
          LastModifiedDate: LastModifiedDate,
        };
        // console.log(data);
        this.ExcelFile = data;
      };
    }
  }

  get formCtls() {
    return this.myForm.controls;
  }
}
