import { Component, OnInit, ViewChild } from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { FormGroup } from "@angular/forms";
import { FormBuilder } from "@angular/forms";
import { Validators } from "@angular/forms";
import { CourseRequestService } from "../../../../services/Admin/course-request.service";
import {
  IRequest,
  IsearchModel,
  RequestData,
} from "app/admin/models/admin/course-request";
import { AuthService } from "app/shared/services/auth/auth.service";

import { MessagesService } from "app/shared/services/messages.service";

@Component({
  selector: "app-employee-requestes",
  templateUrl: "./course-requestes.component.html",
  styleUrls: ["./course-requestes.component.scss"],
})
export class CourseRequestComponent implements OnInit {
  totalItems: number;
  myForm: FormGroup;
  EduCompId: any;

  displayedColumns: string[] = [
    "#",
    "employeeName",
    "courseName",
    "details",
    "status",
    "actions",
  ];

  dataSource: MatTableDataSource<RequestData>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  constructor(
    private fb: FormBuilder,
    private CourseRequestService: CourseRequestService,
    private authserv: AuthService,
    private tostar: MessagesService
  ) {}

  ngOnInit(): void {
    this.myForm = this.fb.group({
      mobile: [""],
      code: [""],
      email: [""],
      approve: [],
    });
    this.EduCompId = this.authserv.getEduCompId();
    this.getAllRequests(1);
  }

  get myFormCtrl() {
    return this.myForm.controls;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  getAllRequests(page: number) {
    let data: IsearchModel = {
      page: page,
      EduCompId: this.EduCompId,
      mobile: this.myFormCtrl.mobile.value,
      code: this.myFormCtrl.code.value,
      account_email: this.myFormCtrl.email.value,
      approvement: this.myFormCtrl.approve.value,
    };
    this.CourseRequestService.getAllRequest<IRequest>(data).subscribe((res) => {
      if (res) {
        this.dataSource = new MatTableDataSource(res.Requests);
        this.totalItems = res.countItems;
      }
    });
  }

  approveCourseRequest(request: RequestData, status: boolean) {
    this.CourseRequestService.ApproveRequest(request.id, status).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          let i = this.dataSource.data.findIndex((ele) => ele == request);
          this.dataSource.data[i].status = status;
          this.tostar.success("General.Done");
        }
      }
    );
  }

  resetSearch() {
    this.myForm.reset();
    this.getAllRequests(1);
  }
}
