  //   }
  // }

  // onSelectDepartment($event) {
  //   $event.value.forEach((id) => {
  //     let idFounded = this.departmentDatesCtrl.value.find(
  //       (date) => date.id == id
  //     );
  //     if (idFounded == undefined) {
  //       let dep = this.departments.find((e) => e.id == id);
