import { DatePipe } from "@angular/common";
import { Component, Inject, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { TranslateService } from "@ngx-translate/core";
import {
  IJobTitleDatesEditVM,
  IJobTitleEditDialogVM,
} from "app/admin/models/setup/job-title";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-edit-job-title-dates",
  templateUrl: "./edit-job-title-dates.component.html",
  styleUrls: ["./edit-job-title-dates.component.scss"],
})
export class EditJobTitleDatesComponent implements OnInit {
  curLang: string;
  myForm: FormGroup;
  submitted: boolean = false;

  jobTitle: IJobTitleEditDialogVM;

  constructor(
    translate: TranslateService,
    private fb: FormBuilder,
    private datePipe: DatePipe,
    public dialogRef: MatDialogRef<EditJobTitleDatesComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { jobTitle: IJobTitleEditDialogVM }
  ) {
    this.curLang = translate.currentLang;
    this.jobTitle = data.jobTitle;
  }

  ngOnInit(): void {
    this.myForm = this.fb.group(
      {
        from_date: [null, [Validators.required]],
        to_date: [null],
      },
      {
        validator: CustomeValidator.dateBefore("from_date", "to_date"),
      }
    );
    this.myForm.patchValue(this.jobTitle);
  }

  editJobTitleDates() {
    this.submitted = true;
    // console.log(this.myForm.value);

    let jobTitleDates: IJobTitleDatesEditVM = {
      from_date: this.datePipe.transform(this.formCtrl?.from_date.value),
      to_date: this.datePipe.transform(this.formCtrl?.to_date?.value),
    };

    if (this.myForm.valid) {
      this.dialogRef.close({
        jobTitle: jobTitleDates,
      });
    }
  }

  get formCtrl() {
    return this.myForm.controls;
  }
}
