import { NgxSpinnerService } from "ngx-spinner";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MessagesService } from "app/shared/services/messages.service";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material/dialog";
import {
  currentSubject,
  IAddedUnitVM,
  IAddUpdateUnitVM,
} from "app/admin/models/admin/setup/course";
import { SubjectsService } from "app/admin/services/Admin/subjects.service";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { TranslateService } from "@ngx-translate/core";

@Component({
  selector: "app-add-unit",
  templateUrl: "./add-unit.component.html",
  styleUrls: ["./add-unit.component.scss"],
})
export class AddUnitComponent implements OnInit {
  unitId: number;
  unit_name: string;
  subjectId: number;
  coverPath = "https://via.placeholder.com/150";

  myForm!: FormGroup;
  submitted = false;
  curLang: string;

  constructor(
    private fb: FormBuilder,
    private msg: MessagesService,
    private spinner: NgxSpinnerService,
    private subjectsService: SubjectsService,
    public uploadFileService: UploadFileService,
    private translate: TranslateService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<AddUnitComponent>
  ) {
    this.subjectId = data.subjectId;
    // console.log("subjectId", this.subjectId);
    this.unitId = data.unitId;
    if (this.unitId > 0) this.getUnitById();

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [this.unitId],
      unit_ar_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      unit_en_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      unitDescription: [""],
      thumbnailPic: [null],
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  // get Unit ById
  getUnitById() {
    this.spinner.show();
    this.subjectsService.getUnitById(this.unitId).subscribe((res: any) => {
      if (res.addedUnit) {
        this.myForm.patchValue(res.addedUnit);
        this.FormCtrls.thumbnailPic.setValue(
          res.addedUnit.unitThumb?.attach_path
        );
        this.unit_name = res.addedUnit.unit_ar_name;
        this.subjectId = res.addedUnit.subjectid;
        // this.coverPath = res.addedUnit.unitThumb?.attach_path;
      }
      this.spinner.hide();
    });
  }

  // Start: add Unit
  addUpdateUnit() {
    this.submitted = true;
    let body = {} as IAddUpdateUnitVM;
    let addedUnit = {} as IAddedUnitVM;
    let currentSubject = {} as currentSubject;

    addedUnit.id = this.FormCtrls.id?.value;
    addedUnit.unit_ar_name = this.FormCtrls.unit_ar_name?.value;
    addedUnit.unit_en_name = this.FormCtrls.unit_en_name?.value;
    addedUnit.unitDescription = this.FormCtrls.unitDescription?.value;
    addedUnit.thumbnailPic = this.FormCtrls.thumbnailPic?.value;
    currentSubject.subject_id = this.subjectId;
    body.addedUnit = addedUnit;
    body.currentSubject = currentSubject;

    if (this.myForm.valid) {
      this.spinner.show();
      this.subjectsService.addUpdateUnit(body).subscribe((res) => {
        // console.log("addUpdateUnit", res);
        if (res) {
          this.dialogRef.close(res);
          this.unitId == 0
            ? this.msg.success("General.AddSuccessfully")
            : this.msg.success("General.UpdeteSuccessfully");
        }
        this.spinner.hide();
      });
    }
  }

  onChange(event: any) {
    this.uploadFileService.uploadFile(event).then((file) => {
      // this.coverPath = file.FileAsBase64;
      this.FormCtrls.thumbnailPic.setValue(file);
    });
  }
}
