import { ActivatedRoute } from "@angular/router";
import { GeneralDropdownVM } from "app/admin/models/admin/educations";
import { NgxSpinnerService } from "ngx-spinner";
import { PointsService } from "./../../../services/Admin/points.service";
import { GeneralService } from "app/shared/services/General/general.service";
import { Component, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import {
  IAddExamPointsVM,
  IAddLecturesPointsVM,
  IExamPointVM,
  ILecturePointVM,
  IPointSystemVM,
} from "app/admin/models/admin/point";
import { teacherByEduCompId } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { ToastrService } from "ngx-toastr";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MatDialog } from "@angular/material/dialog";

@Component({
  selector: "app-points-setup",
  templateUrl: "./points-setup.component.html",
  styleUrls: ["./points-setup.component.scss"],
})
export class PointsSetupComponent implements OnInit {
  Teachers: teacherByEduCompId[];
  subjects: GeneralDropdownVM[];
  lecturePoints: IPointSystemVM[];
  ExamPoints: IPointSystemVM[];
  EduCompId: any;

  lecturesForm!: FormGroup;
  lecsubmitted = false;

  ExamForm!: FormGroup;
  ExamSubmitted = false;

  LecturePage: number = 1;
  ExamPage: number = 1;
  maxSize = 9;
  SubjectId: number;
  teacherUserId: number;
  courseId: number;
  functionId: number;

  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private toastr: ToastrService,
    private authserv: AuthService,
    private GeneralService: GeneralService,
    private PointsService: PointsService,
    private dialog: MatDialog,
    private spinner: NgxSpinnerService
  ) {
    route.paramMap.subscribe((params) => {
      this.courseId = Number(params.get("courseId"));
      console.log(this.courseId);
    });
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
    });
  }

  ngOnInit(): void {
    this.EduCompId = Number(this.authserv.getEduCompId());
    this.getExamPoints();
    this.getLeturePoints();
    this.getTeachersByEducompId();

    this.lecturesForm = this.fb.group({
      ByQuestion: [false, [Validators.required]],
      from: ["", [Validators.required, CustomeValidator.minusNum]],
      to: ["", [Validators.required, CustomeValidator.minusNum]],
      value: [0, [Validators.required, CustomeValidator.minusNum]],
      questionAttempt: [true, [Validators.required]],
      questionAttempts: [0, [CustomeValidator.minusNum]],
      attendancevalue: ["", [Validators.required, CustomeValidator.minusNum]],
      absencevalue: ["", [Validators.required, CustomeValidator.minusNum]],
    });

    this.ExamForm = this.fb.group({
      ByQuestion: [false, [Validators.required]],
      from: ["", [Validators.required, CustomeValidator.minusNum]],
      to: ["", [Validators.required, CustomeValidator.minusNum]],
      value: ["", [Validators.required, CustomeValidator.minusNum]],
    });

    this.changeQuestionAttemptsValidation(true);
    this.ByQuestionValidators(true);
    this.ByQuestionValidatorsLectures(true);
  }

  get lecturesFormCtrls() {
    return this.lecturesForm.controls;
  }

  get ExamFormCtrls() {
    return this.ExamForm.controls;
  }

  addLecturePoint() {
    this.lecsubmitted = true;
    if (this.courseId == null) {
      this.toastr.warning("Please select courseId");
      return;
    }

    // if (this.SubjectId == null) {
    //   this.toastr.warning("Please select Subject");
    //   return;
    // }

    let data: IAddLecturesPointsVM = {
      ByQuestion: this.lecturesFormCtrls?.ByQuestion.value,
      from: null, //this.lecturesFormCtrls?.from.value,
      to: null, // this.lecturesFormCtrls?.to.value,
      selectedSubjectId: this.courseId,
      value: this.lecturesFormCtrls?.value.value,
      absencevalue: this.lecturesFormCtrls?.absencevalue.value,
      attendancevalue: this.lecturesFormCtrls?.attendancevalue.value,
      questionAttempt: this.lecturesFormCtrls?.questionAttempt.value,
      questionAttempts: this.lecturesFormCtrls?.questionAttempts.value,
      isLecture: true,
    };
    console.log("addLecturePoint", data);

    if (this.lecturesForm.valid) {
      this.spinner.show();
      this.PointsService.addPoints(this.EduCompId, data).subscribe(
        (res: any) => {
          //console.log("addLecturePoint", res);

          if (res.returnValue == 1) {
            this.getLeturePoints();
            this.toastr.success("تم إاضافة النقاط");
            this.lecsubmitted = false;
          }
          this.spinner.hide();
        }
      );
    }
  }

  //Exams Points
  addExamPoint() {
    this.ExamSubmitted = true;

    if (this.courseId == null) {
      this.toastr.warning("Please select course");
      return;
    }

    // if (this.SubjectId == null) {
    //   this.toastr.warning("Please select Subject");
    //   return;
    // }

    let data: IAddExamPointsVM = {
      ByQuestion: this.ExamFormCtrls?.ByQuestion.value,
      from: this.ExamFormCtrls?.from.value,
      to: this.ExamFormCtrls?.to.value,
      value: this.ExamFormCtrls?.value.value,
      selectedSubjectId: this.courseId,
      isLecture: false,
    };
    console.log("addExamPoint", data);
    if (this.ExamForm.valid) {
      this.spinner.show();
      this.PointsService.addPoints(this.EduCompId, data).subscribe(
        (res: any) => {
          //console.log("addExamPoint", res);
          if (res.returnValue == 1) {
            this.getExamPoints();
            this.toastr.success("تم إاضافة النقاط");
            this.ExamSubmitted = false;
          }
          this.spinner.hide();
        }
      );
    }
  }

  getLeturePoints() {
    this.spinner.show();
    this.PointsService.getPoints<IPointSystemVM[]>(
      this.courseId,
      6, //type= 6 for lecture
      this.EduCompId
    ).subscribe((res) => {
      if (res) {
        console.log("LeturePoints", res);
        this.lecturePoints = res;
      }
      this.spinner.hide();
    });
  }

  getExamPoints() {
    this.spinner.show();
    this.PointsService.getPoints<IPointSystemVM[]>(
      this.courseId,
      3, //type= 3 for exam
      this.EduCompId
    ).subscribe((res) => {
      if (res) {
        console.log("ExamPoints", res);
        this.ExamPoints = res;
      }
      this.spinner.hide();
    });
  }

  deletePoint(point: IPointSystemVM, type: string) {
    this.spinner.show();
    this.PointsService.deletePoint(point.Id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        if (type == "lecture") {
          let i = this.lecturePoints.findIndex((e) => e == point);
          this.lecturePoints.splice(i, 1);
        } else {
          //type ==Exam
          let i = this.ExamPoints.findIndex((e) => e == point);
          this.ExamPoints.splice(i, 1);
        }

        this.toastr.success("تم الحذف");
      } else this.toastr.error(res.returnString);

      this.spinner.hide();
    });
  }

  DeleatePointDialog(point: IPointSystemVM, type: string): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: `${point.Value}: points`,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deletePoint(point, type);
      });
  }

  //############################################################################

  //######################################################################
  getTeachersByEducompId() {
    this.GeneralService.getTeachersByEducompId(this.EduCompId).subscribe(
      (res: any) => {
        this.Teachers = res.lstTeachers;
      }
    );
  }

  onChangeTeacher(teacheruserId: number) {
    if (teacheruserId != null) {
      this.teacherUserId = teacheruserId;
      this.getExamPoints();
      this.getLeturePoints();
      this.GeneralService.getSubjectByTeacherId(teacheruserId).subscribe(
        (res: any) => {
          this.subjects = res.lsts;
        }
      );
    }
  }

  onChangeSunbject(SubjectId: number) {
    this.SubjectId = SubjectId;
    this.ExamFormCtrls.selectedSubjectId.setValue(SubjectId);
  }

  changeQuestionAttemptsValidation(questionAttempt: boolean) {
    if (questionAttempt == true) {
      this.lecturesForm.controls["questionAttempts"].setValidators([
        Validators.required,
        CustomeValidator.minusNum,
      ]);
    }

    if (questionAttempt == false) {
      this.lecturesForm.controls["questionAttempts"].reset();
      this.lecturesForm.controls["questionAttempts"].clearValidators();
    }
    this.lecturesFormCtrls.questionAttempts.updateValueAndValidity();
  }

  ByQuestionValidators(ByQuestion: boolean) {
    if (ByQuestion == true) {
      this.ExamFormCtrls.from.reset();
      this.ExamFormCtrls.from.clearValidators();
      this.ExamFormCtrls.to.reset();
      this.ExamFormCtrls.to.clearValidators();
    } else {
      this.ExamFormCtrls.from.setValidators([
        Validators.required,
        CustomeValidator.minusNum,
      ]);

      this.ExamFormCtrls.to.setValidators([
        Validators.required,
        CustomeValidator.bigZero,
      ]);
    }
    this.ExamFormCtrls.from.updateValueAndValidity();
    this.ExamFormCtrls.to.updateValueAndValidity();
  }

  ByQuestionValidatorsLectures(ByQuestion: boolean) {
    if (ByQuestion == true) {
      this.lecturesFormCtrls.from.reset();
      this.lecturesFormCtrls.from.clearValidators();
      this.lecturesFormCtrls.to.reset();
      this.lecturesFormCtrls.to.clearValidators();
    } else {
      this.lecturesFormCtrls.from.setValidators([
        Validators.required,
        CustomeValidator.minusNum,
      ]);

      this.lecturesFormCtrls.to.setValidators([
        Validators.required,
        CustomeValidator.bigZero,
      ]);
    }
    this.lecturesFormCtrls.from.updateValueAndValidity();
    this.lecturesFormCtrls.to.updateValueAndValidity();
  }

  track(index: number, point: ILecturePointVM): number {
    return point.Id;
  }
}
