import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";

import { IArticleViewModel } from "./../../../models/admin/setup/articles";

@Injectable({
  providedIn: "root",
})
export class ArticlesService {
  constructor(private http: HttpClient) {}

  getAllArticles<T>(page: number, EduCompId: number) {
    return this.http.get<IArticleViewModel>(
      `${environment.apiURL}Article/geAllArticles/${EduCompId}/${page}`
    );
  }

  addArticle<T, TV>(obj: T) {
    return this.http.post<TV>(`${environment.apiURL}Article/PostArticles`, obj);
  }

  editArticle<T, TV>(obj: T, id: number) {
    return this.http.put<TV>(
      `${environment.apiURL}Article/PutArticles/${id}`,
      obj
    );
  }

  editJobTitleArticle<T, TV>(id: number, obj: T) {
    return this.http.put<TV>(
      `${environment.apiURL}Article/EditArticleDepJob/${id}`,
      obj
    );
  }

  deleteArticle(id: any) {
    return this.http.delete(
      `${environment.apiURL}Article/DeleteArticles/${id}`
    );
  }

  deleteJobTitleArticle(id: any) {
    return this.http.delete(
      `${environment.apiURL}Article/DeleteJobTitleOfArticles/${id}`
    );
  }
}
