import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { GiftsService } from "app/admin/services/Admin/gifts.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "app-gifts-category-add",
  templateUrl: "./gifts-category-add.component.html",
  styleUrls: ["./gifts-category-add.component.scss"],
})
export class GiftsCategoryAddComponent implements OnInit {
  giftcategoryId: number = 0;
  functionId: number;
  myForm!: FormGroup;
  submitted = false;
  currentLang: string;
  EduCompId: any;
  rowFunctions: IRowFunctionVM[];
  giftcategoryName: string;
  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private authserv: AuthService,
    private giftsService: GiftsService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private msg: ToastrService
  ) {
    this.route.queryParamMap.subscribe((params) => {
      this.giftcategoryId = Number(params.get("giftcategoryId"));
      if (this.giftcategoryId > 0) {
        this.getGiftCategoryById(this.giftcategoryId);
      }

      this.functionId = Number(params.get("functionId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //  console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });
  }
  get engNameCtrl() {
    return this.myForm.get("englishName");
  }
  get arbNameCtrl() {
    return this.myForm.get("arabicName");
  }
  get IdCtrl() {
    return this.myForm.get("id");
  }
  ngOnInit() {
    this.EduCompId = this.authserv.getEduCompId();
    this.myForm = this.fb.group({
      id: [""],
      englishName: ["", Validators.required],
      arabicName: ["", Validators.required],
      EduCompId: [this.EduCompId],
    });
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
      //this.getGiftCategoryById(this.giftcategoryId);
    });
    this.getGiftCategoryById(this.giftcategoryId);
  }

  ngAfterViewInit() {
    this.EduCompId = this.authserv.getEduCompId();
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
      console.log(this.currentLang);
      this.getGiftCategoryById(this.giftcategoryId);
    });
  }

  getGiftCategoryById(giftCategoryId: number) {
    this.giftsService
      .getGiftCategoryByID(giftCategoryId)
      .subscribe((res: any) => {
        console.log("getcategoryById", res);
        if (this.currentLang == "ar") {
          this.giftcategoryName = res.arbName;
        } else {
          this.giftcategoryName = res.engName;
        }
        this.giftcategoryId = res.Id;
        console.log("gift category id", res.Id);
        console.log("eng", res?.engName);
        this.IdCtrl?.setValue(res.Id);
        console.log("idc", this.IdCtrl.value);

        this.engNameCtrl.setValue(res?.engName);
        this.arbNameCtrl.setValue(res?.arbName);
      });
  }

  OnAdd() {
    this.submitted = true;
    let valid = this.myForm.valid;
    if (valid) {
      console.log(this.myForm.value);
      this.spinner.show();
      // console.log("form data", this.myForm.value);
      this.giftsService
        .addEditCategory(this.myForm.value)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            if (this.IdCtrl.value == 0) {
              if (this.currentLang == "ar") {
                this.msg.success("تم إضافة الفئة");
              } else {
                this.msg.success("Gift Category Added ");
              }
            } else {
              if (this.currentLang == "ar") {
                this.msg.success("تم تعديل الفئة");
              } else {
                this.msg.success("Gift Category Updated ");
              }
            }
          } else {
            if (this.currentLang == "ar") {
              this.msg.success("هذا الاسم موجود مسبقا ");
            } else {
              this.msg.success("This name is already exist");
            }
          }

          this.spinner.hide();
        });
    }
  }
}
